.App {


  height: 100vh;
  width:100vw;

  font-family: Montserrat;
  position: relative;



}
.App .todoLists {
  margin: 0;
  padding: 0;
  width: 90%;
  height: 100%;
  margin: 0 auto;

  display: grid;
  grid-template-columns: 1fr 1fr;



}
.App .createTodo {
  position: absolute;
  width: 100%;
  top: -80px;
  left: 30%;
   
}
.App . createTodo {
  background: #fff;
}




